import { createStore } from "vuex";
import axios from "axios";
import moment from "moment";
export default createStore({
  state: {
    login: false,
    userInfo: {
      id: "",
      username: "",
      email: "",
      createdAt: "",
      hasMoney: "",
    },
    Community: [{}],
  },
  getters: {
    userInfo: (state) => state.userInfo,
    Communities: (state) => state.Community,
  },
  mutations: {
    async updateUserMoney(state) {
      if (localStorage.getItem("jwt") !== null) {
        await axios("/api/users/me", {
          headers: {
            Authorization: `Bearer ${localStorage.getItem("jwt")}`,
          },
        })
          .then((res) => {
            state.userInfo.hasMoney = res.data.hasMoney;
          })
          .catch(() => {
            // localStorage.removeItem("jwt");
            this.$router.push({ name: "home" });
          });
      }
    },
    loginSuccess(state, value) {
      console.log(value);
      state.userInfo.id = value.id;
      state.userInfo.username = value.username;
      state.userInfo.email = value.email;
      state.userInfo.createdAt = value.createdAt;
      state.userInfo.hasMoney = value.hasMoney;
      state.login = true;
    },
    logout(state) {
      localStorage.removeItem("jwt");
      state.login = false;
    },
    getCommunity(state) {
      axios("/api/posts", {
        headers: {
          Authorization: `Bearer ${localStorage.getItem("jwt")}`,
        },
      })
        .then((res) => {
          let data = res.data.data;
          for (let i = 0; i < data.length; i++) {
            let date = data[i].attributes.createdAt;
            date = moment(moment(date).add("9", "h")).format(
              "YYYY년 M월 DD일" + "." + "HH시 MM분 SS초"
            );
            state.Community.unshift({
              id: data[i].id,
              title: data[i].attributes.title,
              createdAt: date.split("."),
            });
          }
        })
        .catch((err) => {
          console.log(err);
        });

      console.log(state.Community);
    },
  },
  actions: {},
  modules: {},
});
