import { createRouter, createWebHistory } from "vue-router";

const routes = [
  {
    path: "/",
    name: "home",
    component: function () {
      return import("../views/HomeView.vue");
    },
  },
  {
    path: "/holzzak",
    name: "holzzakView",
    component: () => {
      return import("../views/Game/holzzak.vue");
    },
  },
  {
    path: "/community",
    name: "community",
    component: () => {
      return import("../views/Community/CommunityView.vue");
    },
    children: [
      {
        path: ":id",
        name: "communityTable",
        component: () => {
          return import("../views/Community/CommunityTableView.vue");
        },
      },
      {
        path: "write",
        name: "CommunityWrite",
        component: () => {
          return import("../views/Community/CommunityWriteView.vue");
        },
      },
    ],
  },
  {
    path: "/user",
    name: "user",
    component: function () {
      return import("../views/UserView.vue");
    },
    children: [
      {
        path: "login",
        name: "login",
        component: function () {
          return import("../views/LoginView.vue");
        },
      },
      {
        path: "join",
        name: "join",
        component: function () {
          return import("../views/JoinView.vue");
        },
      },
      {
        path: "mypage",
        name: "myPage",
        component: function () {
          return import("../views/myPageView.vue");
        },
      },
      {
        path: "chat",
        name: "chat",
        component: function () {
          return import("../views/chatView.vue");
        },
      },
      {
        path: "deposit",
        name: "deposit",
        component: () => {
          return import("../views/depositView.vue");
        },
      },
      {
        path: "betlog",
        name: "betlog",
        component: () => {
          return import("../views/betLogView.vue");
        },
      },
    ],
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
